import Vue from 'vue'
import Router from 'vue-router'

import HelloWorld from '@/components/HelloWorld.vue'
import ControlPage from '@/views/ControlPage.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
    },
    {
      path: '/planner',
      name: 'ControlPage',
      component: ControlPage,
    },
    {
      path: '/settings',
      name: 'ControlPage',
      component: ControlPage,
    }
  ]
})